import { Component, OnInit , OnDestroy } from '@angular/core';
import {Content} from "../../shared/content";
import {ActivatedRoute}from "@angular/router";
import {Subscription} from "rxjs/Rx";
import {ContentService} from "../content.service";
import {ROUTER_DIRECTIVES} from '@angular/router';



@Component({
  moduleId: module.id,
  selector: 'app-single-content',
  templateUrl: 'single-content.component.html',
  styleUrls: ['single-content.component.css'],
  directives: [ROUTER_DIRECTIVES]
})
export class SingleContentComponent implements OnInit  , OnDestroy{
  article : Content;
  private subscription: Subscription;
  private id: string;
  private sectionParam : string;

  constructor(private activatedRoute: ActivatedRoute ,
              private contentService : ContentService) {}


  ngOnInit() {
    this.subscription = this.activatedRoute.params.subscribe(
      (param:any) => {
        this.sectionParam = param['section'];
        this.id = param['id'];
        this.article = this.contentService.getSingleContent(+this.id);
      });
  }


  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}
